import React from "react";
import Link from 'gatsby-link'

export default () =>
  <div>
    <h1>Merci !</h1>
    <p>Votre message a bien été envoyé. Je vous répondrai dans les meilleurs délais</p>
    <Link to="/">Retour à la page d'accueil</Link>
  </div>


